<template>
    <div id="wrapper">
        <v-head></v-head>
              <div class="tableBar">
                 <div id="table">
                    <el-table
                :data="tableData"
                id="teaCourseTable"
                border
                :default-sort = "{prop: 'name',order: 'descending'}"
                >
                
                <el-table-column
                  prop="academy"
                  label="学院"
                min-width="20%"
                sortable>
                </el-table-column>
                <el-table-column
                  prop="grade"
                  label="年级"
                min-width="20%"
                sortable>
                </el-table-column>
                <el-table-column
                  prop="aclass"
                  label="班级"
                min-width="20%"
                sortable>
                </el-table-column>
                
                
                <el-table-column
                  prop="operation"
                  label="操作"
                min-width="20%">
                <template slot-scope="scope">
            
                 <el-button @click="deleteCourse(scope.row)" type="text" size="medium">删除</el-button>
                </template>
                </el-table-column>
              </el-table>
               <el-button @click="addCourse()" type="primary" size="medium">添加</el-button>
			   <el-button @click="exit()">返回</el-button>
                </div>
             </div>

        </div>
        
    </div>
</template>
<script>
import vHead from '../commons/AdminstratorNav.vue';
export default {
  components:{
      vHead,
  },
  data(){
      return{
          tableData:[{
            
             academy:"软件学院",
             grade:"2015",
             aclass:"6",
             
             
          },
          {
              
              academy:"软件学院",
             grade:"2015",
             aclass:"5",
            
             
          }
          ]
      }
  },
  methods:{
    
    deleteCourse(row){

    },
    addCourse()
    {
          this.$router.push({path: '/adminstrator/adbiCourseClassAdd'})
    },
     exit() {
        this.$router.push({path: '/adminstrator/biCourseManagement'})
      }
  }
}
</script>
<style scoped>
#info{
    width:96%;
    margin:0px auto;
    padding-top:10px;
}
#infoTable{
    border-color:#eefff6;
   
}
#query{

    width:100px;
}


.scoreBoard{
    margin-top: 10px;
    background-color: #eef1f6;
    width:100%;
    height:800px;
}
.lbl{
    font-family: "微软雅黑";
    color:#CDC1C5;
}
#table{
     margin:0px auto;
    margin-top:20px;
    width:95%;
}
</style>



